import DeleteIcon from "@material-ui/icons/Delete";

function Card(props) {
  function Delete() {
    props.onDel(props.id);
  }
  return (
    <div className="card">
      <div className="inside">
        <h2>{props.title}</h2>
        <p>{props.content}</p>
        <h4 onClick={Delete}>
          <DeleteIcon />
        </h4>
      </div>
    </div>
  );
}
export default Card;
